import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { LightPop } from './../../../light/light-out/light-out.module';

@IonicPage()
@Component({
    selector: 'light-popList-page',
    templateUrl: 'light-popList-page.html'
})
export class LightPopListPage {

    constructor(
        public navCtrl: NavController,
        public navParams: NavParams,
        public lightPop: LightPop

    ) {

    }

    //提示
    toBasicUsage() {
        this.lightPop.message('我是一个提示弹窗', '提示');
    }

    //警告
    toErrMessage() {
        this.lightPop.errMessage('我是一个警告弹窗', '警告');
    }

    //确认
    toConfirmation() {
        this.lightPop.confirm('我是一个确认弹窗', '提示').then((data) => {
            if (data) {
                console.log('确定');
            } else {
                console.log('关闭');
            }
        })
    }

}



